'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useNotification } from '@/context/NotificationContext';
import { useRouter } from 'next/navigation';
import PatientSidebar from '@/components/PatientSidebar';
import Loading from '@/components/Loading';
import api from '@/lib/api';
import { format, parseISO, addDays, subDays } from 'date-fns';
import { FiCalendar, FiClock, FiUser, FiChevronLeft, FiChevronRight, FiX, FiFileText, FiMapPin, FiDollarSign } from 'react-icons/fi';

export default function PatientAppointmentsPage() {
  const { user, loading: authLoading, logout } = useAuth();
  const { showNotification } = useNotification();
  const router = useRouter();
  const [appointments, setAppointments] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [selectedAppointment, setSelectedAppointment] = useState<any>(null);
  const [showModal, setShowModal] = useState(false);
  const [activeTab, setActiveTab] = useState<'all' | 'pending' | 'confirmed' | 'completed' | 'cancelled'>('all');

  useEffect(() => {
    if (!authLoading && (!user || user.role !== 'patient')) {
      router.push('/login');
      return;
    }
    if (user && user.role === 'patient') {
      fetchAppointments();
    }
  }, [user, authLoading, router, selectedDate, activeTab]);

  const fetchAppointments = async () => {
    try {
      setLoading(true);
      const dateStr = format(selectedDate, 'yyyy-MM-dd');
      const response = await api.get('/appointments');
      let allAppointments = response.data.appointments || [];

      // Filter by patient ID
      allAppointments = allAppointments.filter((appt: any) => {
        return appt.patient?.id === user?.id || appt.patientId === user?.id;
      });

      // Filter by date
      allAppointments = allAppointments.filter((appt: any) => {
        const apptDate = format(parseISO(appt.appointmentDate), 'yyyy-MM-dd');
        return apptDate === dateStr;
      });

      // Filter by status if not 'all'
      if (activeTab !== 'all') {
        allAppointments = allAppointments.filter((apt: any) => apt.status === activeTab);
      }

      // Sort by time
      allAppointments.sort((a: any, b: any) => {
        const timeA = a.appointmentTime || '00:00';
        const timeB = b.appointmentTime || '00:00';
        return timeA.localeCompare(timeB);
      });

      setAppointments(allAppointments);
    } catch (error) {
      console.error('Error fetching appointments:', error);
      showNotification('Failed to fetch appointments', 'error');
    } finally {
      setLoading(false);
    }
  };

  const handleCancelAppointment = async (appointmentId: string) => {
    try {
      await api.put(`/appointments/${appointmentId}/status`, { status: 'cancelled' });
      showNotification('Appointment cancelled successfully', 'success');
      fetchAppointments();
      setShowModal(false);
    } catch (error: any) {
      showNotification(error.response?.data?.message || 'Failed to cancel appointment', 'error');
    }
  };

  const getStatusBadgeColor = (status: string) => {
    switch (status) {
      case 'confirmed':
        return 'bg-green-100 text-green-800';
      case 'pending':
        return 'bg-yellow-100 text-yellow-800';
      case 'completed':
        return 'bg-blue-100 text-blue-800';
      case 'cancelled':
        return 'bg-red-100 text-red-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const handleDateChange = (days: number) => {
    const newDate = new Date(selectedDate);
    newDate.setDate(selectedDate.getDate() + days);
    setSelectedDate(newDate);
  };

  const handleAppointmentClick = (appointment: any) => {
    setSelectedAppointment(appointment);
    setShowModal(true);
  };

  if (authLoading || loading) {
    return <Loading />;
  }

  if (!user || user.role !== 'patient') {
    return null;
  }

  return (
    <div className="flex min-h-screen bg-gray-50">
      <PatientSidebar user={user} logout={logout} />
      <main className="ml-64 flex-1 transition-all duration-300">
        <div className="p-8">
          <div className="mb-8">
            <h1 className="text-3xl font-bold text-gray-800 mb-2">My Appointments</h1>
            <p className="text-gray-600">View and manage your appointments</p>
          </div>

          {/* Date Navigation */}
          <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100 mb-6">
            <div className="flex items-center justify-between mb-4">
              <button
                onClick={() => handleDateChange(-1)}
                className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <FiChevronLeft className="text-xl text-gray-600" />
              </button>
              <div className="text-center">
                <h2 className="text-xl font-bold text-gray-800">
                  {format(selectedDate, 'EEEE, MMMM d, yyyy')}
                </h2>
                <button
                  onClick={() => setSelectedDate(new Date())}
                  className="text-sm text-teal-600 hover:text-teal-700 mt-1"
                >
                  Today
                </button>
              </div>
              <button
                onClick={() => handleDateChange(1)}
                className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <FiChevronRight className="text-xl text-gray-600" />
              </button>
            </div>
          </div>

          {/* Status Tabs */}
          <div className="flex gap-2 mb-6 overflow-x-auto pb-2">
            {[
              { id: 'all', label: 'All' },
              { id: 'pending', label: 'Pending' },
              { id: 'confirmed', label: 'Confirmed' },
              { id: 'completed', label: 'Completed' },
              { id: 'cancelled', label: 'Cancelled' },
            ].map((tab) => (
              <button
                key={tab.id}
                onClick={() => setActiveTab(tab.id as any)}
                className={`px-6 py-2 rounded-lg font-medium transition-colors whitespace-nowrap ${
                  activeTab === tab.id
                    ? 'bg-teal-600 text-white'
                    : 'bg-white text-gray-700 hover:bg-gray-100 border border-gray-200'
                }`}
              >
                {tab.label}
              </button>
            ))}
          </div>

          {/* Appointments List */}
          <div className="space-y-4">
            {appointments.length === 0 ? (
              <div className="bg-white rounded-xl shadow-sm p-12 border border-gray-100 text-center">
                <FiCalendar className="text-5xl text-gray-300 mx-auto mb-4" />
                <p className="text-gray-500 text-lg mb-2">No appointments found</p>
                <p className="text-gray-400 text-sm">
                  {activeTab !== 'all' 
                    ? `No ${activeTab} appointments for this date`
                    : 'No appointments scheduled for this date'}
                </p>
              </div>
            ) : (
              appointments.map((appointment) => (
                <div
                  key={appointment.id}
                  onClick={() => handleAppointmentClick(appointment)}
                  className="bg-white rounded-xl shadow-sm p-6 border border-gray-100 hover:shadow-md transition-shadow cursor-pointer"
                >
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <div className="flex items-center gap-4 mb-3">
                        <div className="w-12 h-12 rounded-full bg-gradient-to-br from-teal-400 to-indigo-500 flex items-center justify-center text-white font-bold">
                          {appointment.doctor?.user?.name?.charAt(0)?.toUpperCase() || 'D'}
                        </div>
                        <div className="flex-1">
                          <h3 className="text-lg font-semibold text-gray-800">
                            Dr. {appointment.doctor?.user?.name || 'Doctor'}
                          </h3>
                          <p className="text-sm text-gray-600">{appointment.doctor?.specialization || 'General'}</p>
                        </div>
                        <span
                          className={`px-3 py-1 rounded-full text-xs font-semibold ${getStatusBadgeColor(
                            appointment.status
                          )}`}
                        >
                          {appointment.status}
                        </span>
                      </div>

                      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-4">
                        <div className="flex items-center gap-2 text-gray-600">
                          <FiClock className="text-teal-600" />
                          <span className="text-sm">{appointment.appointmentTime}</span>
                        </div>
                        {appointment.doctor?.consultationFee && (
                          <div className="flex items-center gap-2 text-gray-600">
                            <FiDollarSign className="text-teal-600" />
                            <span className="text-sm">${appointment.doctor.consultationFee}</span>
                          </div>
                        )}
                        {appointment.reason && (
                          <div className="flex items-center gap-2 text-gray-600">
                            <FiFileText className="text-teal-600" />
                            <span className="text-sm truncate">{appointment.reason}</span>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
      </main>

      {/* Appointment Details Modal */}
      {showModal && selectedAppointment && (
        <div
          className="fixed inset-0 bg-black bg-opacity-60 backdrop-blur-sm flex items-center justify-center z-50 p-4"
          onClick={() => {
            setShowModal(false);
            setSelectedAppointment(null);
          }}
        >
          <div
            className="bg-white rounded-2xl shadow-2xl w-full max-w-2xl max-h-[90vh] overflow-y-auto"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Modal Header */}
            <div className="px-6 py-5 border-b border-gray-200 sticky top-0 bg-white">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <div className="w-10 h-10 rounded-lg bg-teal-600 flex items-center justify-center flex-shrink-0">
                    <FiCalendar className="text-xl text-white" />
                  </div>
                  <div>
                    <h2 className="text-xl font-bold text-gray-900">Appointment Details</h2>
                    <p className="text-sm text-gray-500">
                      {format(parseISO(selectedAppointment.appointmentDate), 'MMMM d, yyyy')}
                    </p>
                  </div>
                </div>
                <button
                  onClick={() => {
                    setShowModal(false);
                    setSelectedAppointment(null);
                  }}
                  className="text-gray-400 hover:text-gray-600 hover:bg-gray-100 rounded-lg p-1.5 transition-all"
                >
                  <FiX className="text-lg" />
                </button>
              </div>
            </div>

            {/* Modal Content */}
            <div className="px-6 py-5 space-y-6">
              {/* Doctor Info */}
              <div className="bg-gray-50 rounded-xl p-4 border border-gray-200">
                <div className="flex items-center gap-4">
                  <div className="w-16 h-16 rounded-full bg-gradient-to-br from-teal-400 to-indigo-500 flex items-center justify-center text-white font-bold text-xl">
                    {selectedAppointment.doctor?.user?.name?.charAt(0)?.toUpperCase() || 'D'}
                  </div>
                  <div className="flex-1">
                    <h3 className="text-lg font-bold text-gray-900">
                      Dr. {selectedAppointment.doctor?.user?.name || 'Doctor'}
                    </h3>
                    <p className="text-sm text-gray-600">{selectedAppointment.doctor?.specialization || 'General'}</p>
                    {selectedAppointment.doctor?.department && (
                      <div className="flex items-center gap-2 mt-2 text-sm text-gray-600">
                        <FiMapPin className="text-teal-600" />
                        <span>
                          {typeof selectedAppointment.doctor.department === 'string'
                            ? selectedAppointment.doctor.department
                            : selectedAppointment.doctor.department?.name || 'N/A'}
                        </span>
                      </div>
                    )}
                  </div>
                  <span
                    className={`px-3 py-1 rounded-full text-xs font-semibold ${getStatusBadgeColor(
                      selectedAppointment.status
                    )}`}
                  >
                    {selectedAppointment.status}
                  </span>
                </div>
              </div>

              {/* Appointment Details */}
              <div className="space-y-4">
                <div className="flex items-center gap-3 p-3 bg-white border border-gray-200 rounded-lg">
                  <FiCalendar className="text-teal-600 text-xl" />
                  <div>
                    <p className="text-sm text-gray-500">Date</p>
                    <p className="font-semibold text-gray-800">
                      {format(parseISO(selectedAppointment.appointmentDate), 'EEEE, MMMM d, yyyy')}
                    </p>
                  </div>
                </div>

                <div className="flex items-center gap-3 p-3 bg-white border border-gray-200 rounded-lg">
                  <FiClock className="text-teal-600 text-xl" />
                  <div>
                    <p className="text-sm text-gray-500">Time</p>
                    <p className="font-semibold text-gray-800">{selectedAppointment.appointmentTime}</p>
                  </div>
                </div>

                {selectedAppointment.doctor?.consultationFee && (
                  <div className="flex items-center gap-3 p-3 bg-white border border-gray-200 rounded-lg">
                    <FiDollarSign className="text-teal-600 text-xl" />
                    <div>
                      <p className="text-sm text-gray-500">Consultation Fee</p>
                      <p className="font-semibold text-gray-800">${selectedAppointment.doctor.consultationFee}</p>
                    </div>
                  </div>
                )}

                {selectedAppointment.reason && (
                  <div className="p-3 bg-white border border-gray-200 rounded-lg">
                    <p className="text-sm text-gray-500 mb-2">Reason for Visit</p>
                    <p className="text-gray-800">{selectedAppointment.reason}</p>
                  </div>
                )}
              </div>

              {/* Action Buttons */}
              {selectedAppointment.status !== 'cancelled' && selectedAppointment.status !== 'completed' && (
                <div className="flex gap-3 pt-4 border-t border-gray-200">
                  <button
                    onClick={() => {
                      setShowModal(false);
                      setSelectedAppointment(null);
                    }}
                    className="flex-1 px-5 py-2.5 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 font-medium transition-all"
                  >
                    Close
                  </button>
                  <button
                    onClick={() => handleCancelAppointment(selectedAppointment.id)}
                    className="flex-1 px-5 py-2.5 bg-red-600 text-white rounded-lg hover:bg-red-700 font-medium transition-all"
                  >
                    Cancel Appointment
                  </button>
                </div>
              )}

              {selectedAppointment.status === 'cancelled' && (
                <div className="pt-4 border-t border-gray-200">
                  <button
                    onClick={() => {
                      setShowModal(false);
                      setSelectedAppointment(null);
                    }}
                    className="w-full px-5 py-2.5 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 font-medium transition-all"
                  >
                    Close
                  </button>
                </div>
              )}

              {selectedAppointment.status === 'completed' && (
                <div className="pt-4 border-t border-gray-200">
                  <button
                    onClick={() => {
                      setShowModal(false);
                      setSelectedAppointment(null);
                    }}
                    className="w-full px-5 py-2.5 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 font-medium transition-all"
                  >
                    Close
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}


